<template>
	<div id="home">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="center">
				{{title}}
			</div>
			<div slot="right">
			</div>
		</nav-bar>
		<!--第二个菜单描述-->
		<nav-desc/>
		<!--滚动--->
		<scroll class="home-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
			<div class="p8">
				<!--图片导航菜单-->
				<manager-content/>
				<!--轮播图-->
				<swiper/>
				<!--发布商品-->
				<!--功能菜单-->
				<multi-function-nav/>
				<!--商品列表-->
				<goods-list :role-arr="roleArr"/>
			</div>
		</scroll>
	</div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'
import { Button } from 'vant';




import NavDesc from './childComps/NavDesc'
import Swiper from 'components/common/swiper/Swiper'
import MultiFunctionNav from './childComps/MultiFunctionNav'
import Scroll from 'components/common/scroll/Scroll'
import GoodsList from 'components/contents/goods/GoodsList'
import {getHomeMultiData} from 'network/home'


import ManagerContent from 'components/contents/managercontent/ManagerContent'
import {itemListenerMixin} from 'common/mixin'	


export default {
  name: 'Home',
  components:{
	NavBar,
	NavDesc,
	Swiper,
	MultiFunctionNav,
	GoodsList,
	Scroll,
	ManagerContent,
	[Button.name]:Button,
  },
  mixins:[itemListenerMixin],
  data(){
	return{
		roleArr:[],
		title:'管理APP',
		index:0
	}
  },
  methods:{
	loadMove(){
		this.getHomeMultiData()				//获取数据
		this.$refs.scroller.refresh();		//刷新一下
		this.$refs.scroller.finishPullUp()	//获取最新高度
	},
	getHomeMultiData(){
		getHomeMultiData().then(res=>{
			this.roleArr.push(...res)
		})
	},
	publishOfGoods(){
		this.$router.push("/publishofgoods").catch(err=>{err})
	},
  },
  created(){
	this.getHomeMultiData();
	this.$dialog.alert({
			title:'您好！',
			message: '欢迎开始淫荡的一天..',
		}).then(() => {
		// on close
	});
  },
}
</script>

<style scoped>
